import { Directive, Output, EventEmitter, HostListener, Input, ElementRef, OnInit } from '@angular/core';

@Directive({
  selector: '[appScrollBottom]',
  standalone: true,
})
export class ScrollBottomDirective implements OnInit {
  @Output() scrollingFinished = new EventEmitter<void>();
  @Input() threshold = 60;
  private window!: Window;
  constructor(private el: ElementRef) {}

  ngOnInit() {
    this.window = window;
  }

  @HostListener('window:scroll', ['$event.target'])
  windowScrollEvent() {
    const heightOfWholePage = this.window.document.documentElement.scrollHeight;
    const heightOfElement = this.el.nativeElement.scrollHeight;
    const currentScrolledY = this.window.scrollY;
    const innerHeight = this.window.innerHeight;
    const spaceOfElementAndPage = heightOfWholePage - heightOfElement;
    const scrollToBottom = heightOfElement - innerHeight - currentScrolledY + spaceOfElementAndPage;
    if (scrollToBottom < this.threshold) {
      this.scrollingFinished.emit();
    }
  }
}
